<template>
  <div class="message" v-bind:class="{error: message.isError && message.content, added: !message.isError && message.content}">{{message.content}}</div>
</template>

<script>
export default {
  name: 'Notification',
  computed: {
    message() {
      if(this.$store.state.message.length > 0) return this.$store.state.message[0]
      return {}
    }
  }
}
</script>

<style>
  .message {
    width: 98vw;
    height: 4vh;
    display: table-cell;
    border: none;
    margin-bottom: 1vh;
    margin: auto;
    vertical-align: middle;
    text-align: center;
  }
  .error {
    color: red;
    border: 2px solid red;
  }
  .added {
    color: rgb(86, 197, 64);
    border: 1px solid rgb(86, 197, 64);
  }
</style>
