<template>
  <div class="nav">
    <router-link to="/" id="otsikko" class="solu">Blog application</router-link>
    <nav id="linkit" class="solu">
      <router-link to="/users">Users</router-link> |
      <router-link to="/blogs">Blogs</router-link>
    </nav>
    <span id="logged" class="solu" v-if="loggedUser">
      {{loggedUser.username }} logged in
      <button id="nappi" v-on:click="toggleVisibility">{{formVisibility}}</button>
      <button v-on:click="logOut" id="nappi">Log out</button>
    </span>
  </div>
</template>

<script>
  export default {
    name: 'navBar',
    computed: {
      loggedUser(){
        return this.$store.state.loggedUser
      },
      formVisibility() {
        if(!this.$store.state.formVisibility) return "Create new"
        else return "Don't create"
      }
    },  
    methods: {
      logOut(){
        window.localStorage.removeItem('loggedBlogUser')
        this.$store.dispatch('notify', {content: 'You have logged out, bye bye', isError: false})
        setTimeout(() => {
          this.$store.commit('LOG_OUT')
          this.$router.push('/')
        }, 3000)
      },
      toggleVisibility(){
        this.$store.commit('CHANGE_FORM_VISIBILITY', !this.$store.state.formVisibility)
      }
    }
  }
</script>

