<template>
  <div class="singleUser">
  <Notification></Notification>
  <h2>{{user.username}}</h2>
  <h3>Added blogs</h3>
  <table>
    <tbody v-for="blog in user.blogs" v-bind:key="blog._id">
      <tr>
        <td>
          <router-link :to="{name: 'singleBlog', params: {id: blog._id}}" id="link">
            <span id="title">{{blog.title}}</span>
          </router-link>
          <span id="author">{{blog.author}}</span> 
        </td>
      </tr>
    </tbody>
  </table>
  <AddBlogForm v-if="visibility"></AddBlogForm>
  </div>
</template>

<script>
import AddBlogForm from '@/components/AddBlogForm'
import Notification from '@/components/Notification'
export default {
  name: 'singleUser',
  props: ['id'],
  components: {
    AddBlogForm,
    Notification
  },
  computed: {
    user() {
      return this.$store.state.users.find(u => u.id === this.id)
    },
    visibility() {
      return this.$store.state.formVisibility
    }
  },
  created(){
    if(!window.localStorage.getItem('loggedBlogUser')) this.$router.push('/')
  }
}
</script>

<style scoped>
  table {
    margin: auto;
  }
  #title {
    color: blueviolet;
  }
  #title:hover {
    color: blue;
  }
  #author {
    color: blue;
    margin-left: 2vw;
  }
  #link {
    text-decoration: none;
  }
</style>
