import Vue from 'vue'
import Vuex from 'vuex'
import blogService from '@/services/blogs.js'
import userService from '@/services/users.js'
import createPersistedState from 'vuex-persistedstate'

Vue.use(Vuex)

export default new Vuex.Store({
  plugins: [createPersistedState()],
  state: {
    loggedUser: null,
    users: [],
    blogs: [],
    formVisibility: false, 
		message: []
  },
  mutations: {
    ADD_BLOG: (state, blog) => {
      state.blogs.push(blog)
      const newUser = state.users.find(user => user.id === blog.user._id)
      const threeFieldBlog = {_id: blog.id, title: blog.title, author: blog.author}
      newUser.blogs.push(threeFieldBlog)
      const arr = state.users.filter(user => user.id !== blog.user._id)
      arr.push(newUser)
      state.users = arr
      state.formVisibility = false
		},
    CHANGE_FORM_VISIBILITY: (state, status) => {
      state.formVisibility = status
    },
    INIT_BLOGS: (state, blogs) => {
      state.blogs = blogs.sort((a, b) => b.likes - a.likes)
    },
    INIT_USERS: (state, users) => {
      state.users = users
    },
    LOG_IN: (state, user) => {
      state.loggedUser = user
    },
    LOG_OUT: (state) => {
      state.loggedUser = null
      state.users = []
      state.blogs = []
      state.message = []
      state.formVisibility = false
    },
    ADD_MESSAGE: (state, message) => {
      const arr = state.message
      arr.push(message)
      state.message = arr
			// state.message.push(message)
		},
		REDUCE_MESSAGE: (state) => {
      const arr = state.message
      arr.shift()
      state.message = arr
			// state.message.shift()
		},
    REMOVE_BLOG: (state, removed) => {
			console.log(removed)
      const filteredBlogs = state.blogs.filter(blog => blog.id !== removed.id)
      state.blogs = filteredBlogs
			const user = state.users.find(u => u.id === removed.user._id)
			console.log('Poistetun lisj: ',user)
			const newBlogs = user.blogs.filter(b => b._id !== removed.id) 
			console.log('blogit poiston jlkeen: ',newBlogs)
      user.blogs = newBlogs
      const filteredUsers = state.users.filter(u => u.id !== user.id)
			const added = filteredUsers.concat(user)
      state.users = added
		},
    UPDATE_BLOG: (state, updated) => {
      const filtered = state.blogs.filter(blog => blog.id !== updated.id)
      const added = filtered.concat(updated)
      state.blogs = added.sort((a, b) => b.likes - a.likes)
    }
  },
  actions: {
    async addBlog (context, blog) {
      const newBlog = await blogService.create(blog)
      if(!newBlog.hasOwnProperty('error')) {
        context.commit("ADD_BLOG", newBlog)
        context.dispatch("notify", {content: "You added new blog; '" + newBlog.title + "' by " + newBlog.author, isError: false})
      } else {
        context.dispatch("notify", {content: 'There was an error: ' + newBlog.error, isError: true})
      }
    },
    async commentBlog (context, comment) {
      console.log(comment)
      const updated = await blogService.addComment(comment)
      if(!updated.hasOwnProperty('error')) {
        context.commit("UPDATE_BLOG", updated)
        context.dispatch("notify", {content: "You commented blog '" + updated.title + "' by " + updated.author, isError: false})
      } else {
        context.dispatch("notify", {content: 'There was an error: ' + newBlog.error, isError: true})
      }
    },
    async initBlogs (context) {
      const blogs = await blogService.getAll()
      if(!blogs.hasOwnProperty('error')) {
        context.commit("INIT_BLOGS", blogs)
      } else {
        context.dispatch("notify", {content: 'There was an error: ' + blogs.error, isError: true})
      }
    },
    async likeBlog (context, id) {
      const updated = await blogService.likeBlog(id)
      if(!updated.hasOwnProperty('error')) {
				context.commit("UPDATE_BLOG", updated)
        context.dispatch("notify", {content: "You liked a blog '" + updated.title + "' by " + updated.author, isError: false})
      } else {
        context.dispatch("notify", {content: 'There was an error: ' + updated.error, isError: true})
      }
    },
    async removeBlog (context, id) {    
      const removed = await blogService.remove(id)
      console.log('poistettu: ',removed)
      if(!removed.hasOwnProperty('error')){
        context.commit("REMOVE_BLOG", removed)
        context.dispatch('notify', {content: "You removed blog '" + removed.title + "' by " + removed.author, isError: false})
      } 
      else context.dispatch("notify", {content: 'There was an error: ' + removed.error, isError: true})
    },
    async initUsers (context) {
      const users = await userService.getUsers()
      if(!users.hasOwnProperty('error')) context.commit("INIT_USERS", users)
      else context.dispatch("notify", {content: 'There was an error: ' + users.error, isError: true})
		},
		notify (context, message) {
			context.commit('ADD_MESSAGE', message)
			setTimeout(() => {
				context.commit('REDUCE_MESSAGE')
			}, 5000)
		}
  }
})
