import axios from 'axios'
const baseUrl = '/api/blogs'

let token = null

const getAll = () => {
  try {
    const request = axios.get(baseUrl)
    return request.then(response => response.data)
  } catch (error) {
    return error.response.data
  }
}

const getByUser = async (id) => {
  try {
    const response = await axios.get(`${baseUrl}/user/${id}`)
    return response.data
  } catch (error) {
    return error.response.data
  }
}

const getOne = async (id) => {
  try {
    const response = await axios.get(`${baseUrl}/blog/${id}`)
    return response.data
  } catch (error) {
    return error.response.data
  }
}

const setToken = (newToken) => {
  token = `bearer ${newToken}`
}
const printToken = () => {
  console.log(token)
}

const create = async (newBlog) => {
  const user = JSON.parse(window.localStorage.getItem('loggedBlogUser')) 
  const bearer = `bearer ${user.token}`
  const config = {
    headers: { 'authorization': bearer }
  }
  try {
    const response = await axios.post(baseUrl, newBlog, config)
    return response.data
  } catch (error) {
    return error.response.data
  }
}

const remove = async (id) => {
  const user = JSON.parse(window.localStorage.getItem('loggedBlogUser')) 
  const bearer = `bearer ${user.token}`
  const config = {
    headers: { 'authorization': bearer }
  }
  try {
    const response = await axios.delete(`${baseUrl}/${id}`, config)
    return response.data
  } catch (error) {
    return error.response.data
  }
}

const addComment = async (com) => {
  const user = JSON.parse(window.localStorage.getItem('loggedBlogUser')) 
  const bearer = `bearer ${user.token}`
  const config = {
    headers: { 'authorization': bearer }
  }
  const body = {'comment': com.comment}
  try {
    const response = await axios.put(`${baseUrl}/${com.id}/comment`, body, config)
    return response.data
  } catch (error) {
    return error.response.data
  }
}

const likeBlog = async (id) => {
  try {
    const response = await axios.put(`${baseUrl}/${id}/like`)
    return response.data
  } catch (error) {
    return error.response.data
  }
}

export default { getAll, create, addComment, setToken, remove, getByUser, addComment, getOne, likeBlog }

