        <tr>
          <td id="solu1">
            <router-link :to="{name: 'singleUser', params: {id: user.id}}" id="solu1">{{user.username}}</router-link>
          </td>
          <!-- <router-link :to="{path: 'users/'+user.id}" tag="td" id="solu1">{{user.username}}</router-link> -->
          <td id="solu2">{{user.blogs.length}}</td>
        </tr>
      </tbody>
    </table>
    <AddBlogForm v-if="visibility"></AddBlogForm>
  </div>
</template>

<script>
import AddBlogForm from '@/components/AddBlogForm'
import Notification from '@/components/Notification'
export default {
  name: 'users',
  components: {
    AddBlogForm,
    Notification
  },
  computed: {
    users() {
      return this.$store.state.users
    },
    visibility() {
      return this.$store.state.formVisibility
    }
  },
  created(){
    if(!window.localStorage.getItem('loggedBlogUser')) this.$router.push('/')
  }
}
</script>

