<template>
  <div>
    <Notification></Notification>
      <div id="blogs">
      <h2>Blogs</h2>
      <table>
        <tbody v-for="(blog, index) in blogs" v-bind:key="index">
          <tr>
            <td>
              <router-link :to="{name: 'singleBlog', params: {id: blog.id}}" id="link">
                <span id="title">{{blog.title}}</span>
              </router-link>
              <span id="author">{{blog.author}}</span> 
            </td>
          </tr>
        </tbody>
      </table>
      <AddBlogForm v-if="visibility"></AddBlogForm>
    </div>
  </div>
</template>

<script>
import AddBlogForm from '@/components/AddBlogForm.vue'
import Notification from '@/components/Notification.vue'
export default {
  name: 'blogs',
    components: {
    AddBlogForm,
    Notification
  },
  computed: {
    blogs() {
      return this.$store.state.blogs
    },
    visibility() {
      return this.$store.state.formVisibility
    }
  },
  created(){
    if(!window.localStorage.getItem('loggedBlogUser')) this.$router.push('/')
  }
}
</script>

