<template>
  <div class="addBlogForm">
    <h2>Lis blogi</h2>
    <form @submit.prevent="addBlog" autoComplete="on" v-if="loggedUser">
      <table class="taulu">
        <tbody>
          <tr>
            <td class="vasen">Title:</td>
            <td class="oikea"><input type='text' v-model="title"></td>
          </tr>
          <tr>
            <td class="vasen">Author:</td>
            <td class="oikea"><input type='text' v-model="author"></td>
          </tr>
          <tr>
            <td class="vasen">URL:</td>
            <td class="oikea"><input type='text' v-model="url"></td>
          </tr>
        </tbody>
      </table>
      <button id="nappi" type="submit">Add Blog</button>
    </form>
  </div>
</template>

<script>
  export default {
    name: 'addBlogForm',
    data() {
      return {
        title: '',
        author: '',
        url: ''
      }
    },
    computed: {
      loggedUser(){
        return this.$store.state.loggedUser
      }
    },
    methods: {
      addBlog() {
        const newBlog = {title: this.title, author: this.author, url: this.url, comments: []}
        this.$store.dispatch('addBlog', newBlog)
        this.title = ''
        this.author = ''
        this.url = ''
        this.$router.push('/blogs')
      }
    }
  }
</script>

<style scoped>
  .taulu {
    width: 40vw;
    margin: auto;
  }
  .vasen {
    width: 8vw;
  }
  .oikea {
    width: 32vw;
  }
  input {
    width: 32vw;
  }
  #nappi {
    margin-top: 2vh;
    background-color: cyan;
    border-radius: 0.5vh;
    color: black;
  }
  #nappi:hover {
    background-color: ivory;
  }
</style>


