<template>
  <div id="singleBlog">
    <Notification/>
    <h2>{{blog.title}} {{blog.author}}</h2>
    <div class="solu"><a :href="'//' + blog.url" target="_blank">{{blog.url}}</a></div>
    <div class="solu">{{blog.likes}} likes <button @click="like">Like</button></div>
    <div class="solu">added by
      <router-link :to="{name: 'singleUser', params: {id: blog.user._id}}" class="link">{{blog.user.username}}</router-link>
       <button v-if="deleteVisibility" @click="remove">Delete</button>
    </div>
    <div>
      <h3>Comments</h3>
      <button v-on:click="addComment" id="add">{{commentVisibility ? "Don't add" : "Add comment"}}</button>
    </div>
    <div v-if="commentVisibility">
      <textarea v-model="comment" rows="5" placeholder="max 150 characters"></textarea>
      <button v-on:click="send">Send</button>
    </div>
      <div>
        <div v-for="comment in blog.comments" v-bind:key="comment._id" class="comment">
          - <span id="green">{{comment.content}}</span> by 
          <router-link :to="{name: 'singleUser', params: {id: comment.userId}}" class="link">{{comment.username}}</router-link>
        </div>
      </div>
    <AddBlogForm v-if="formVisibility"></AddBlogForm>
  </div>
</template>

<script>
import AddBlogForm from '@/components/AddBlogForm'
import Notification from '@/components/Notification'
export default {
  name: 'SingleBlog',
  props: ['id'],
  data() {
    return {
      comment: '',
      commentVisibility : false
    }
  },
  components: {
    AddBlogForm,
    Notification
  },
  computed: {
    blog() {
      return this.$store.state.blogs.find(blog => blog.id === this.id)
    },
    formVisibility() {
      return this.$store.state.formVisibility
    },
    loggedUser() {
      return this.$store.state.user
    },
    deleteVisibility() {
      return this.blog.user._id === this.$store.state.loggedUser.id
    }
  },
  methods: {
    addComment() {
      this.commentVisibility = !this.commentVisibility
      this.comment = ''
    },
    send() {
      console.log(this.blog)
      if(this.comment.length > 150) {
        this.$store.dispatch('notify', {content: 'Too long comment, max 150 characters', isError: true})
      } 
      else {
        if (this.comment.length > 0) {
          const com = {id: this.id, comment: this.comment}
          this.$store.dispatch('commentBlog', com)
          this.comment = ''
          this.commentVisibility = false
        } else {
          this.$store.dispatch('notify', {content: 'Empty comment field', isError: true})
        }
      }
    },
    like(){
      this.$store.dispatch('likeBlog', this.blog.id)
    },
    remove(){
      if(this.blog.user._id === this.$store.state.loggedUser.id) this.$store.dispatch('removeBlog', this.blog.id)
      this.$router.push('/blogs')
    }
  },
  created(){
    if(!window.localStorage.getItem('loggedBlogUser')) this.$router.push('/')
  }
}
</script>

