<template>
  <div class="home">
    <Notification></Notification>
    <h2>Log in to application</h2>
		<form @submit.prevent="logIn" autoComplete="on">
			<div>Kyttjtunnus:</div>
			<div><input type="text" v-model="username" placeholder="username" autoComplete="shipping username"></div><br>
			<div>Salasana:</div>
			<div><input type="password" v-model="password" placeholder="password" autoComplete="shipping password"></div><br>
			<button id="nappi" type="submit">Log in</button>
		</form>
  </div>
</template>

<script>
  import blogService from '@/services/blogs'
  import loginService from '@/services/login'
  import Notification from '@/components/Notification'

  export default {
    name: 'home',
    data () {
      return {
        username: '',
        password: ''
      }
    },
    components: {
      Notification
    },
    computed: {
      loggedUser() {
        return this.$store.state.loggedUser
      }
    },
    methods: {
      async logIn() {
        try {
          const user = await loginService.login({username: this.username, password: this.password})
          this.$store.commit('LOG_IN', user)
          this.$store.dispatch('notify', {content: 'You have logged in as '+user.username+' and will be redirected...', isError: false})
          this.$store.commit("CHANGE_FORM_VISIBILITY", false)
          this.$store.dispatch('initUsers')
          this.$store.dispatch('initBlogs')
          this.username = ''
          this.password = ''
          blogService.setToken(user.token)
          window.localStorage.setItem('loggedBlogUser', JSON.stringify(user))
          setTimeout( () => {
            this.$router.push('/users')
          }, 3000)
          } catch (exception) {
            console.log(exception)
            this.$store.dispatch('notify', {content: 'Invalid password or username', isError: true})
          }
      }
    },
    created() {
      const loggedUserJSON = window.localStorage.getItem('loggedBlogUser')
      if (loggedUserJSON) {
        const user = JSON.parse(loggedUserJSON)
        this.$store.commit('LOG_IN', user)
        this.$store.dispatch('notify', {content: 'You have logged in as ' + user.username + ' and will be redirected...', isError: false})
        this.$store.commit("CHANGE_FORM_VISIBILITY", false)
        this.$store.dispatch('initUsers')
        this.$store.dispatch('initBlogs')
        blogService.setToken(user.token)
        setTimeout( () => {
          this.$router.push('/users')
        }, 3000)
      }
    }
  }
</script>

<style scoped>
  #nappi {
    background-color: blue;
    border-radius: 0.5vh;
    color: white;
  }
  #nappi:hover {
    background-color: green;
  }
  form {
    margin-top: 1vh;
  }
</style>

